import React, { useEffect, useState } from 'react';
import DatePicker from "react-datepicker";
import TextField from '@material-ui/core/TextField';
import "react-datepicker/dist/react-datepicker.css";


export default function Gfrom() {

  const gUrl = "https://77docs.google.com/forms/d/e/1FAIpQLSc35mVaI0DscNFmRGA0NBzupg4hsJCM1uFv18rq1MxFzio3QA/";
  const[submitted, SetSubmitted] = useState(false);
  const [startDate, setStartDate] = useState(new Date());
  const [startDate2, setStartDate2] = useState(new Date());
  const [ShowForm, setShowForm] = useState(1);
const [Submit, SetSubmit] = useState(0);

  

  useEffect(() => {
    if (Submit){
    let Prom = new Promise(resolve => {
      resolve( setTimeout(() => { setShowForm(1); }, 3000) );

    });
    Prom.then(setShowForm(0));
    }
}, [Submit]); 

  return  ShowForm ? (
    <>
      <form name="gform" id="gform" encType="text/plain"
        action={gUrl + 'formResponse?'} target="hidden_iframe"
        onSubmit={()=> {SetSubmit(Submit + 1); SetSubmitted(true)}}
        className="From-div">
        <div>

          <input type="text" name="entry.1085456845" id="entry.1085456845" placeholder="First name" required={true} />

          <input type="text" name="entry.1240593602" id="entry.1240593602" placeholder="Last name" required={true}/>
        </div>
        <div>

          <input type="text" placeholder="Email" name="entry.1873303376" id="entry.1873303376" required={true}/>
          <input type="number" placeholder="Mobile number" name="entry.999363338" id="entry.999363338" required={true}/>

        </div>
        <div>
          <select name="entry.1328044103" defaultValue={'Compact'} required={true}>
            <option id="entry.1328044103" value="Compact" disabled >Vehicle</option>
            <option id="entry.1328044103" value="Compact">Compact</option>
            <option id="entry.1328044103" value="Mid-Size">Mid-Size</option>
          </select>
        </div>

        <>
          <label htmlFor="Pickup">Pickup Date and Time: </label>
        </>
        <div>
          <DatePicker
            selected={startDate}
            onChange={date => setStartDate(date)}
            showTimeSelect
            timeFormat="HH:mm"
            timeIntervals={15}
            timeCaption="time"
            dateFormat="MMMM d, yyyy h:mm aa"
            name="entry.883892745"
            id="entry.883892745"
            
          />
        </div>
        <>
          <label htmlFor="Return">Return Date and Time: </label>
        </>
        <div>
          <DatePicker
            selected={startDate2}
            onChange={date => setStartDate2(date)}
            showTimeSelect
            timeFormat="HH:mm"
            timeIntervals={15}
            timeCaption="time"
            dateFormat="MMMM d, yyyy h:mm aa"
            name="entry.662059231"
            id="entry.662059231"
          />
        </div>

        <>
          <label htmlFor="Return">message </label>
        </>
        <div>
          <TextField
            id="entry.755355107"
            multiline
            rows="4"
            name="entry.755355107"
            margin="normal"
            variant="outlined"

          />
        </div>


        <input type="submit" value="Submit" />
      </form>

      <iframe name="hidden_iframe" id="hidden_iframe" style={{ display: 'none' }} onLoad={()=>submitted ? {} : {}} title="myFrame"></iframe>
     
    </>
  ) :  ( <div className="From-div" >Submited</div>);
}